.woocommerce-error, .woocommerce-info, .woocommerce-message {
  box-shadow: 0px 2px 10px 0px #f0f0f0;
  background: white;
  font-size: $font-size-body;
  margin-bottom: 20px;
  display: inline-block;
  width: 100%;

  li {
    margin: 0;
    max-width: 96%;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &:before {
    content: "\E828";
    font-family: growtype-icons;
    font-size: 18px;
    top: 0;
    left: 20px;
    bottom: 0;
    margin: auto;
    height: 27px;
  }

  .btn-close {
    position: absolute;
    right: 35px;
    top: 17px;
    font-size: 18px;
    color: var(--text-color);
    cursor: pointer;
    @include bp-phone-xl() {
      right: 15px;
      top: 15px;
    }
  }
}

.alert {
  &.woocommerce-info {
    border-top: 3px solid $color-info;
    padding: 1em 2em 1em 3.5em;
    max-width: $max-width-container;
    margin-left: auto;
    margin-right: auto;
  }

  &.woocommerce-message {
    border-top: 3px solid $color-success;
    padding: 1em 2em 1em 3.5em;

    &::before {
      color: $color-success;
    }
  }

  &.woocommerce-error {
    border-top: 3px solid $color-error;
    padding: 1em 2em 1em 3.5em;
  }
}

.woocommerce-notices-wrapper {
  .woocommerce-info {
    display: inline-block;
    width: 100%;
  }
}
